<template>
    <div id='box'>
      <div class="top">
        <div class="left80">
          <a :name="'list'+index" v-for="(item, index) in second" :key="index" :class="isact==index?'act':''" @click="isact=index;isshow=true;getdata(item.id)">{{item.name}}</a>
        </div>
        <div class="coll">
          <van-icon :name="isshow?'arrow-down':'arrow-up'" size="25px" @click="getcoll()"/> 
          <div class="coll-show" v-show="!isshow">
            <div style="padding:5pt;font-size:13pt;">全部分类</div>
            <a :href="'#list'+index" v-for="(item, index) in second" :key="index" class="ellipsis" @click="isact=index;isshow=!isshow;getdata(item.id)">{{item.name}}</a>
          </div>
        </div>
      </div>

      <!-- 数据列表  对应二级分类 -->
      <van-list
        v-model="loading"
        :finished="finished"
        finished-text="没有更多了"
        @load="onLoad"
      >
        <div class="list_data pd3">
          <div class="goods" @click="$router.push({path:'/goodsDetail',query:{id:item.goods_id}})" v-for="(item,index) in list" :key="index">
            <img :src="item.original_img" alt="" class="img">
            <div class="title ell1">{{item.goods_name}}</div>
            <div class="bottom">
              <div class="left">
                <span class="price">¥{{item.shop_price}}</span>
                <span class="market">¥{{item.market_price}}</span>
                <p class="count">{{item.sales_sum}}件已售</p>
              </div>
            </div>
          </div>
        </div>
      </van-list>
      <!-- 前往购物车 -->
      <img src="@/assets/images/mall/mall_cart.png" alt="" class="cart" @click="$router.push({path:'/cart'})">
    </div>
</template>

<script>
import {  Icon, List } from 'vant'
import { geterji } from '@/axios/getData'
export default {
    components: {
      vanIcon: Icon,
      vanList: List
    },
    props: {

    },
    data() {
        return {
          isact:0,
          isshow:true,
          second:[],  // 
          list:[],
          id:0,
          page:1,
          loading: false,
          finished: false,
          count:0
        }
    },
    created() {
      this.second = JSON.parse(this.$route.query.second);
      this.id = this.$route.query.id
      // this.getdata();
    },
    methods: {
      getcoll(){
        this.isshow = !this.isshow;
      },
      getdata(a){
        this.page = 1;
        this.list = [];
        this.id = a
        this.loading = true,
        this.finished = false,
        this.onLoad()
      },
      async onLoad(a=this.$route.query.id){
        if(this.loading){
          var res = await geterji({id:this.id, page: this.page});
          if( this.page == 1 ){
            this.list = res.data.goods_list.list
            this.count = res.data.goods_list.count
          } else {
            this.list = this.list.concat(res.data.goods_list.list)
            this.count = res.data.goods_list.count
          }
          this.loading = false
          this.page +=1
          if(this.count<0){
            this.finished = true
          }
          console.log(res.data, this.list)
        }
      }
    },
    computed: {

    },
    mounted() {

    },
};
</script>

<style scoped lang="scss">
.top{
  display: flex;
  align-items: center;
}
.left80{
  width:90%;
  box-shadow:-1px 0px 2px #eee inset,   /*左边阴影*/ 
            0px 0px 0px transparent,  /*上边阴影*/ 
            0px 0px 0px transparent,  /*右边阴影*/ 
            0px 0px 0px transparent;/*下边阴影*/ 
  // background: yellow;
  white-space:nowrap;
  overflow: scroll;
  a{
    display: inline-block;
    margin-left:10pt;
    padding:10pt 0;
  }
}
.coll{
  position: relative;
  width:10%;
  display: flex;
  justify-content: center;
  .coll-show{
    top:130%;
    left:-900%;
    width:1000%;
    position: absolute;
    background: #fff;
    a{
      display: inline-block;
      width:20%;
      text-align: center;
      padding:2pt 0;
      color:#000;
    }
  }
}
.act{
  position: relative;
  color:#15BDDB;
}
.act::after{
  display: block;
  content:'';
  position: absolute;
  background: #15BDDB;
  width:100%;
  height:1.5pt;
  bottom:5pt;
}
a{
  font-size: 12pt;
}

/* 数据列表 */
.list_data{
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  .goods{
    width:47%;
    .img{
      width:120pt;
      height:125pt;
    }
    .title{
      margin:5pt 0;
      width:100%;
      font-size: 12pt;
      color:#000;
      font-weight: 600;
    } 
    .bottom{
      display: flex;
      justify-content: space-between;
      align-items: center;
      .left{
        .price{
          color:#EE241E;
          font-weight: 600;
          font-size: 11pt;
        }
        .market{
          text-decoration: line-through;
          color:#7b7b7b;
          font-size: 9pt;
        }
      }

    }  
  }
}
.goods + .goods{
  margin-bottom: 30pt;
}
/* 前往购物车的图标 */
.cart{
  position: fixed;
  top:80%;
  right:5%;
  width:40pt;
  height:40pt;
}

</style>



